.ListControls {
    width: 50%;
    background-color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 1px #ccc;
            box-shadow: 0 2px 1px #ccc;
    margin: 10px 20%;
    padding: 10px 0;
}



@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}



